.footer {
    padding: 0 8%;

}
.first__footer {
    margin-top: 5rem;
    display: flex;
    justify-content: center;
    align-items:center;
    flex-direction: column;
    gap: 3rem;
}
.first__footer span{
    font-size: 2rem;
}
.sub__footer {
    font-size: 5rem;
    font-family: $tektur-fonts;
    font-weight: 900;
    text-shadow: $font-neon-text-swadow;
}
.second__footer{
    margin-top: 10rem;
    height: 5rem;
}
.second__footer span {
    display: block;
    font-size: 2rem;
    text-align: center;
}