.portfolio {
    margin-top: 4rem;
    padding: 5rem 8%; 
}
.sub__portfolio {
    font-size: 5rem;
    font-family: $tektur-fonts;
    font-weight: 900;
    text-align: center;
    text-shadow: $font-neon-text-swadow;
    margin-bottom: 5rem;

}
.portfolio__box{
    display: flex;
    justify-content: center;
    align-items:center;
    flex-direction: column;
    margin: 2rem auto;
}
.portfolio__box ul{
    display: flex;
    justify-content: center;
    align-items:center;
    gap: 2rem;
    flex-wrap: wrap;
    margin-bottom: 2rem;
}
.portfolio__box ul li{
   list-style: none;
   padding: 1.2rem 2rem;
   cursor: pointer;
   color:$text-color;
   font-size: 3rem;
   font-family: $tektur-fonts;
   font-weight: 500;
   transition: color 0.4 ease-in;
}

.portfolio__box ul li.active{
    color:$accent-color;
 }
.products{
    display: flex;
    justify-content: center;
    align-items:center;
    flex-wrap: wrap;
}
.itemBox {
    position: relative;
    width: 30rem;
    height: 21.1rem;
    margin: 5px;
    display: block;
    overflow: hidden;
    border: 2px solid $accent-color;
    border-radius:10px;

}

.products .itemBox.hide{
    display:none;
    transition: all 0.4s ease-in;
}
.products .itemBox img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: all 0.4s ease-in;
}
.products .itemBox img:hover{
    transform: scale(1.1);
}