.skills {
    margin: 10rem 0;
    padding: 2rem 29%;
}
.sub__skills {
    font-size: 5rem;
    font-family: $tektur-fonts;
    font-weight: 900;
    text-align: right;
    text-shadow: $font-neon-text-swadow;
    margin-bottom: 5rem;
}

.skils__block h2{
    font-size: 2rem;
    margin-top: 4rem;
    margin-bottom: 1rem;
}
.progress-bar {
    background-color:$text-color;
    width: 100%;
    height: 1.5rem;
    border-radius:5px;
}
.progress-bar div{
    height: 10px;
    border-radius:5px;
    height: 1.5rem;
    width: 50%;
    background:$bg-gradient-color;
}
.progress-bar div span{
    height: 5rem;
    width: 5rem;
    border-radius:50%;
    background:$bg-color;
    border:3px solid $accent-color;
    float: right;
    margin-right: -2rem;
    margin-top: -1.5rem;
    color: $text-color;
    display: flex;
    align-items:center;
    justify-content: center;
    font-size: 1.8rem;
}

.progress-bar .blender {
    width: 90%;
}

.progress-bar .photoshop {
    width: 60%;
}
.progress-bar .aft-eff {
    width: 75%;
}
.progress-bar .unity {
    width: 85%;
}
.progress-bar .max{
    width: 95%;
}