@font-face{
    font-family: Tektur;
    src:url(/fonts/Tektur-Black.woff2);
    font-weight: 900;
}
@font-face{
    font-family: Tektur;
    src:url(/fonts/Tektur-SemiBold.woff2);
    font-weight: 500;
}
@font-face{
    font-family: Poppins;
    src:url(/fonts/Poppins-Regular.woff2);
    font-weight: 400;
}

$bg-color: #04090F;
$accent-color:#EC1D68;
$accent-color2:#C2368C;
$text-color:#fff;
$font-neon-text-swadow: 0 0 10px rgba(235, 30, 105, 0.952),
0 0 20px rgba(235, 30, 105, 0.952),
0 0 30px rgba(235, 30, 105, 0.952),
0 0 40px rgba(235, 30, 105, 0.952),
0 0 70px rgba(235, 30, 105, 0.952);
$bg-gradient-color:linear-gradient(180deg,
    $accent-color 10%,
    $accent-color2 90%);
$tektur-fonts: Tektur;
$poppins-fonts: Poppins;

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: $poppins-fonts;
    font-weight: 400;
    color:$text-color;

}
html{
    font-size: 62.5%;
}
body{
    background:$bg-color;
}
body._lock{
    overflow: hidden;
}
main{
    overflow: hidden;
}
a{
    text-decoration: none;
}
ul,li{
    list-style: none;
}
button{
    font-size: inherit;
    border: none;
    background: none;
}
img{
    display: block;
    width: 100%;
}


@import "./blocks/header.scss";
@import "./blocks/home.scss";
@import "./blocks/about.scss";
@import "./blocks/skills.scss";
@import "./blocks/service.scss";
@import "./blocks/portfolio.scss";
@import "./blocks/form.scss";
@import "./blocks/footer.scss";
@import "./blocks/query.scss";


