.header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10;
    background-color:$bg-color;
    border-bottom: 2px solid $accent-color2;
}
.header__container{
    margin: 0 auto;
    padding: 0 8%;
    display: flex;
    align-items:center;
    justify-content: space-between;
    min-height: 7rem;
}
.header__logo{
    font-size: 3rem;
    z-index: 5;
    font-family:$tektur-fonts;
    color:$text-color;
    font-weight: 900;
}
.header__logo span{
    margin-left: .5rem;
    font-size: 3rem;
    font-family: $tektur-fonts;
    font-weight: 900;
    background:$bg-gradient-color;
    -webkit-background-clip: text;
    color: transparent;
}

.menu__icon{
    display: none;
}

.menu__list{
    display: flex;
    align-items:center;
    font-size: 2rem;
    gap:2.9rem
}
.menu__link{
    color:$text-color;
    font-family: $tektur-fonts;
    font-weight: 500;
    transition: all 0.3s ease-in;
}
.menu__link:hover{
    text-shadow: $font-neon-text-swadow;
    color:$accent-color;
}
.menu__language {
    z-index: 5;
    display: flex;
    gap: 2rem;
    font-size: 2rem;
}
.language__en,
.language__ru {
    font-family: $tektur-fonts;
    font-weight: 500;
    transition: color 0.3s ease-in;
    cursor: pointer;
}

.language__en:hover,
.language__ru:hover{
    color:$accent-color;
}