.form {
    margin-top: 2.5rem;
    padding: 0 8%;
    padding-bottom: 2rem;
}
.sub__form {
    font-size: 5rem;
    font-family: $tektur-fonts;
    font-weight: 900;
    text-align: center;
    text-shadow: $font-neon-text-swadow;
    margin-bottom: 5rem;
}
form{
    display: flex;
    justify-content: center;
    align-items:center;
    flex-direction: column;
    gap: 2rem;
}
form input{
    outline: none;
    width: 60rem;
    max-width: 100%;
    font-size: 3rem;
    height: 6.2rem;
    border: 2px solid $accent-color;
    color:$accent-color;
    background:transparent;
    padding-left: 2rem;
    border-radius:1rem;
}
input::placeholder {
    color:$text-color;
    font-size: 2.5rem;
    font-family: $tektur-fonts;
    font-weight: 500;
    padding-left: 2rem;
    transition: opacity .4s linear;
  }
  input:focus::placeholder{
    opacity: 0;
  }
  textarea{
    outline: none;
    width: 60rem;
    max-width: 100%;
    font-size: 3rem;
    padding-top: 1.5rem;
    height: 25rem;
    border: 2px solid $accent-color;
    color:$accent-color;
    background:transparent;
    padding-left: 1.8rem;
    border-radius:1rem;
    resize: none;
  }

  textarea::placeholder{
    color:$text-color;
    font-size: 2.5rem;
    font-family: $tektur-fonts;
    font-weight: 500;
    padding-left: 2rem;
    transition: opacity .4s linear;
  }
  textarea:focus::placeholder{
    opacity: 0;
  }

  .form button{
    margin-top: 4rem;
    font-size: 3rem;
    font-family: $tektur-fonts;
    font-weight: 900;
    width: 25rem;
    height: 7.6rem;
    border:2px solid $accent-color;
    border-radius: 1rem;
    cursor: pointer;
    transition: all .5s linear;
  }

  .form button:hover{
    background:$bg-gradient-color;
    -webkit-background-clip: text;
    color: transparent;
    letter-spacing: 1px;
    font-size: 3.5rem;
    border:2px solid $text-color;
  }