@media(max-width:767px){
    
    .menu__icon{
        z-index: 5;
        display: block;
        position: relative;
        width: 3rem;
        height: 2rem;
        cursor: pointer;
    }
    .header__menu {
        order: 1;
    }

    .menu__icon span,
    .menu__icon::before,
    .menu__icon::after{
        position: absolute;
        left: 0;
        height: 10%;
        width: 100%;
        transition:  all 0.3s ease;
        background-color:$text-color;
    }
    .menu__icon::after,
    .menu__icon::before{
        content:'';
    }
    .menu__icon::before{
        top: 0;
    }
    .menu__icon::after{
        bottom:0;
    }
    .menu__icon span{
        top:  50%;
        width: 70%;
        background:$bg-gradient-color;
        transform: scale(1)  translate(0px, -50%);
    }
    .menu__icon._active span{
        transform: scale(0)  translate(0px, -50%);
    }
    .menu__icon._active::before{
        top: 50%;
        transform: rotate(-45deg) translate(0px, -50%);
        box-shadow: $font-neon-text-swadow;
    }
    .menu__icon._active::after{
        bottom: 50%;
        transform: rotate(45deg) translate(0px, 50%);
        box-shadow: $font-neon-text-swadow;
    }
    .menu__body{
        position: fixed;
        top: 0;
        left: -100%;
        width: 100%;
        height: 100%;
        background-color:$bg-color;
        padding-top: 100px;
        padding-right: 1rem;
        transition:  left 0.3s ease;
        overflow: auto;
    }
    .menu__body._active{
        left: 0;
    }
    .menu__list{
        display: flex;
        flex-direction: column;
        align-items:flex-start;
        margin-left: 20px;
    }
}
