.about{
    margin-top: 5rem;
    padding-top: 4rem;
    padding-bottom: 2rem;
}
.container__about {
    padding: 0 15%;
}
.sub__section {
    font-size: 5rem;
    font-family: $tektur-fonts;
    font-weight: 900;
    text-align: center;
    text-shadow: $font-neon-text-swadow;
    margin-bottom: 2rem;
}
.about__container {
    margin-top: 7.8rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: space-around;
    align-items: center;
    gap: 3rem;
}
.about__image {
    position: relative;
    justify-self: center;
}
.img__box {
    position: relative;
    width: 35rem;
    height: auto;
}
.liquid-shape {
    position: absolute;
    top: 15%;
    z-index: -1;
    width: 40rem;
    height: auto;
}
.liquid-shape:nth-child(2){
    filter: blur(60px);
}
.about__info {
    justify-self: center;
}
.about__info_name,
.about__info_age,
.about__info_based{
    display: block;
    font-size: 3rem;
    font-weight: 500;
    font-family: $tektur-fonts;
    margin-bottom: 1.5rem;
}
.about__info_name span,
.about__info_age span,
.about__info_based span{
    font-size: 2.5rem;
    font-weight: 400;
    font-family: $poppins-fonts;
   
}

.about__info_text {
    display: block;
    font-size: 1.6rem;
    margin-bottom: 1.5rem;
}
