.service {
    padding: 5rem 0;
}
.sub__service {
    font-size: 5rem;
    font-family: $tektur-fonts;
    font-weight: 900;
    text-align: center;
    text-shadow: $font-neon-text-swadow;
    margin-bottom: 12rem;
}
.service__container-big{
    position: relative;
    margin: 0 auto;
    display: grid;
    width: 110rem;
}
.service__container{
    padding: 1rem 3rem;
    position: relative;
    border-radius:10px;
    width: 50rem;
    border: 3px solid $accent-color;

}
.right-container{
    justify-self: flex-end;
}
.left-container{
    left: 0;
}
.service__container-big::after{
    content:'';
    position: absolute;
    width: 6px;
    height: 110%;
    border: 3px solid $accent-color;
    border-radius: 5px;
    top: -5%;
    left: 50%;
    transform: translateX(-50%);
}

.service__title {
    font-family: $tektur-fonts;
    font-weight: 500;
    font-size: 2rem;
    margin-bottom: 1.3rem;
    
}
.service__text {
    font-size: $poppins-fonts;
    font-size: 1.6rem;
}
.service__left-arrow{
    height: 0;
    width: 0;
    position: absolute;
    top: 28px;
    z-index: 1;
    border-top: 19px solid transparent;
    border-bottom: 19px solid transparent;
    border-left: 30px solid $accent-color;
    right: -30px;
}
.service__right-arrow{
    height: 0;
    width: 0;
    position: absolute;
    top: 28px;
    z-index: 1;
    border-top: 19px solid transparent;
    border-bottom: 19px solid transparent;
    border-right: 30px solid $accent-color;
    left: -30px;
}