
.home {
    padding: 10rem 0;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items:center;
    padding: 0 8%;
    // overflow-x: hidden;
}

.home__container {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
    align-items: center;
}


.home__left {
    display: grid;
}
.home__subtitle{
    font-size: 3rem;
    color:$text-color;
    font-family: $tektur-fonts;
    font-weight: 500;
    margin-bottom: 1.5rem;
    animation-name: moveInLeft;
    animation-duration: 2s;
}
.home__title {
    position: relative;
    font-size: 7.5rem;
    font-family: $tektur-fonts;
    font-weight: 900;
    margin-bottom: 1.5rem;
    animation-name: moveInRight;
    animation-duration: 2s;
}
.home__title span{
    position: relative;
    font-size: 7.5rem;
    font-family: $tektur-fonts;
    font-weight: 900;
    margin-bottom: 1.5rem;
    background:$bg-gradient-color;
    -webkit-background-clip: text;
    color: transparent;
    
}
.home__text {
    font-size: 1.8rem;
    color:$text-color;
    margin-bottom: 3.5rem;
}
.social__home-block {
    justify-self: center;
    width: 30rem; 
    display: flex;
    justify-content: space-between;
    align-self: center;
    cursor: pointer;
    background:transparent;
}
.social-link{
    position: relative;
    width: 5rem;
    height: 5rem;
    border-radius:100%;
    transition: all 3s linear;
}

.social-link:hover{
    background:$bg-gradient-color;
}
.social-link svg{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 4rem;
    background:transparent;
}

/* Блог картинка и текст по кругу */
.home__right{
    position: relative;
    display: flex;
    justify-content: center;
    align-items:center;
}

.circle{
   position: relative;
   width:45rem;
   height: 45rem;
   border-radius:50%;
   display: flex;
   justify-content: center;
   align-items:center;
   animation: flyText 10s ease-in-out infinite;
}
.home-img{
    position: absolute;
    width: 40rem;
    height: 40rem;
    border: 4px solid $accent-color;
    background:url("/images/home.png");
    border-radius: 50%;
    background-size:cover;
}
.home__text-circle{
    position: absolute;
    background:transparent;
    width: 100%;
    height: 100%;
    animation: rotateText 20s linear infinite;
}
.home__text-circle span{
    position: absolute;
    background:transparent;
    left:50%;
    font-size: 1.8rem;
    transform-origin: 0 22.5rem;
}

@keyframes moveInLeft{
    0%{
        opacity: 0;
        transform:  translateX(-100px);
    }
    80%{
        transform:  translateX(20px);
    }
    100%{
        opacity: 1;
        transform:  translateX(0);
    }
}

@keyframes moveInRight{
    0%{
        opacity: 0;
        transform:  translateX(100px);
    }
    80%{
        transform:  translateX(-20px);
    }
    100%{
        opacity: 1;
        transform:  translateX(0);
    }
}

@keyframes rotateText{
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
        
    }
}
@keyframes flyText{
    0%{
        transform: translateY(0px);
    }
    50%{
        transform: translateY(50px);
    }
    100%{
        transform: translateY(0px);
    }
}